import { Component } from '@angular/core';

@Component({
  selector: 'ngfortrackby',
  templateUrl: `./ngfortrackby.component.html`,
  styleUrls : ['./ngfortrackby.component.css']
})
export class ngfortrackbyComponent { 
    employees : any[] ;

      constructor() {
        this.employees = [
        {  code : 'emp01' , name : 'tom'},
        { code : 'emp02' , name : 'jay'},       
        { code : 'emp03' , name : 'parvathi'}        
            ];  
      }

     getEMployess() : void {

      this.employees = [
        {  code : 'emp01' , name : 'tom'},
        { code : 'emp02' , name : 'jay'},       
        { code : 'emp03' , name : 'parvathi'},
        { code : 'emp04' , name : 'jaimin'}    
      ];
     }


     trackByEmpCode(index:number , employee : any) : string {

      return employee.code;
     }
     



}
